<script>
export default {
    name: 'Header',
    data() {
        return {};
    },
    methods: {
        logout(){
            console.log(sessionStorage.getItem('token'))
            sessionStorage.removeItem('token');
            sessionStorage.removeItem('username');
            this.$router.push({ name: 'Login' })
        }
    },

}
</script>

<style >
.header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    padding: 30px 10px;

}
.header__link {
    max-width: 175px;
    width: 100%;
    color: #fff;
    text-decoration: none;
    border-radius: 10px;
    padding: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 40px; 
    background: #4C78FA;
    /*transition: all .3s ease;*/
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 27px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.header__link svg {
    padding-right:10px;
    width: 35px;
    fill: #fff;
    background: transparent;
     /*transition: all .3s ease;*/
}
.header__link:hover svg,
.header__link:hover {
    background: #fff;
    color: #4C78FA;
    fill:  #4C78FA;
}
@media screen and (max-width: 480px) {
    .header {
        padding: 20px 5px;
    }
    .header__link {
        margin-right: 10px;
        font-size: 18px;
    }
    .header__link svg {
        width: 25px;
    }
}
</style>