<template>
<div>
    <Header />
    <preloader :loading="showPreloader" />
    <div class="settings">
        <h1 class="settings__title">API Controller</h1>
        <div class="api-controller">
            <input type="username" v-model="apiName" name="User" placeholder="Name" class="api-controller-field api-controller-field--name" required>
            <input type="apiKey" v-model="apiKey" name="Key" placeholder="Token" class="api-controller-field api-controller-field--token" required>
            <input type="button" name="apiBtn" value="+" class="api-controller-btn" @click="addUser()">
        </div>
        <h2 class="settings__title-list">Api List</h2>
        <div class="api-list">
            <div v-for="(token, name) in this.keys" :key="token" class="api-list__item">
                <div class="api-list__name">{{name}}</div>
                <div class="api-list__token">{{token}}</div>
                <div class="api-list__delete" @click="removeUser(name)">
                    <span class="api-list__delete-txt">+</span>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import Header from '@/components/Header.vue'
import Preloader from '../components/Preloader.vue'

export default {
    name: 'Setting',
    components: {
        Header,
        Preloader
    },
    data() {
        return {
            user: {
                name: '',
                token: '',
            },
            showPreloader: false,
            apiName: '',
            apiKey: '',
            keys: Object
        }
    },
    created() {
        this.user.name = sessionStorage.getItem('username');
        this.user.token = sessionStorage.getItem('token');
        this.showUser();
    },
    methods: {
        showUser() {
            this.showPreloader = true
            this.axios.post('https://api.notasscum.com/show_api', {
                username: this.user.name,
                token:  this.user.token,
            }).then(res => {
                return res.data
            }).then(data => {
                if(data.success ) {
                    this.keys = data.data
                    this.showPreloader = false

                }
                else {
                    console.log(data);
                    this.showPreloader = false;
                    alert(data.data)
                }
            })
        },
        addUser() {
            if(this.apiName && this.apiKey) {
                this.axios.post('https://api.notasscum.com/add_api', {
                    username: this.user.name,
                    token:  this.user.token,
                    apiKey: this.apiKey,
                    apiName: this.apiName
                }).then(res => {
                    return res.data
                }).then(data => {
                    if( data.success ) {
                        this.showUser();
                    }
                    else {
                        console.log(data)
                        alert(data.data)
                    }
                })
            } 
        },
        removeUser(name) {
            if (confirm("Удалить API ключ?")) {
                this.axios.post('https://api.notasscum.com/delete_api', {
                    token:  this.user.token,
                    username: this.user.name,
                    apiName: name
                }).then(res => {
                    return res.data
                }).then(data => {
                    if( data.success ) {
                        this.showUser();
                    }
                    else {
                        alert(data.data)
                    }
                    console.log(data.data);
                })
            }
        }
    }
}
</script>

<style>
.settings {
    max-width: 1170px;
    width: 100%;
    margin: 40px auto;
    box-sizing: border-box;
    padding: 60px;
    color: #3E3E3E;
    border: 1px solid #3D4BE4;
    border-radius: 30px;
    background: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(4px);
}
.api-list {
    display: flex;
    flex-flow: column nowrap;
}
.settings__title,.settings__title-list {
    margin: 0;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 51px;
    line-height: 62px;
    text-align: left;
    padding-bottom: 40px;
}
.api-controller-btn {
    border: 1px solid #4C78FA;
    box-sizing: border-box;
    border-radius: 11px;
    width: 50px;
    height: 50px;
    line-height: 1px;
    font-size: 60px;
    font-family: Montserrat;
    font-weight: 200;
    cursor: pointer;
    color: #4C78FA;
    background: #fff;
    margin: 0 20px 0 30px;
    transition: all 0.3s ease;
}
.api-controller-btn:hover {
    background: #4C78FA;
    color: #fff;
}
.api-controller {
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-grow: 1;
    padding-bottom: 70px;
}
.api-controller-field {
    height: 50px;
    margin: 0;
    width: 100%;
    padding: 5px 15px;
    max-width: 400px;
    font-size: 30px;
    color: #4C78FA;
    border: 1px solid #4C78FA;
    box-sizing: border-box;
    border-radius: 11px;
    transition: all 0.3s ease-in-out;
}
.api-controller-field:focus {
    box-shadow: none;
    background-position: 12px -30px;
    outline:  #4C78FA auto 2px
}
.api-controller-field::placeholder {
    color: #4C78FA;
}
.api-controller-field--name {
    background: url(../assets/user.svg) no-repeat scroll 12px center;
    padding-left: 40px;
}
.api-controller-field--token {
    max-width: 450px;
    background: url(../assets/key.svg) no-repeat scroll 12px center;
    padding-left: 40px;
}
.api-list__item {
    display: flex;
    align-items: center;
    border-radius: 15px;
    overflow: hidden;
    margin: 12px 0;

}
.api-list__name {
    font-family: Montserrat;
    font-weight: 300;
    display: flex;
    max-width: 350px;
    width: 100%;
    background: #4C78FA;
    padding: 10px;
    font-size: 33px;
    line-height: 40px;
    color: #fff;
}
.api-list__token {
    padding: 5px 15px;
    overflow: auto hidden;
    font-size: 20px;
    line-height: 40px;
    box-sizing: border-box;
    height: 60px;
    border: 1px solid #CFCFCF;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}
.api-list__delete {
    border: 1px solid #4C78FA;
    box-sizing: border-box;
    border-radius: 11px;
    width: 50px;
    height: 50px;
    cursor: pointer;
    background: #4C78FA;
    margin: 0 20px 0 40px;
    color: #fff;
    transition: all 0.3s ease;
}
.api-list__delete-txt {
    display: block;
    font-family: Montserrat;
    width: inherit;
    height: inherit;
    font-weight: 200;
    font-size: 40px;
    line-height: 50px;
    transform-origin: center;
    transform: rotate(-45deg);
}
.api-list__delete:hover {
    background: #fff;
    color: #4C78FA;
}
@media screen and (max-width: 768px) {
    .settings {
        padding: 15px;
    }
    .settings__title {
        font-size: 30px;
        padding: 10px;
    }
    .settings__title-list {
        font-size: 30px;
        padding-bottom: 10px;
    }
    .api-controller {
        flex-flow: column wrap;
        align-items: center;
        padding-bottom: 20px
    }
    .api-controller-field {
        margin: 10px 0;
    }
    .api-controller-btn {
        margin: 0 auto;
        padding: 0 20px;
        text-align: center;
        width: auto;
    }
    .api-list__name,.api-list__token {
        font-size: 16px;
    }
    .api-list__delete {
        margin: 5px;
    }
}
</style>