<template>
  <div class="timeframe"> 
    <div class="timeframe_elem" @click="getDate('today','btn1')" :class="{active: activeBtn === 'btn1'}" :disabled="isDisable"><span>Today</span></div>
    <div class="timeframe_elem" @click="getDate('yesterday','btn2')" :class="{active: activeBtn === 'btn2'}" :disabled="isDisable"><span>Yesterday</span></div>
    <div class="timeframe_elem" @click="getDate('this-month','btn5')" :class="{active: activeBtn === 'btn5'}" :disabled="isDisable" ><span>This Month</span></div>
 <!--
    <div class="timeframe_elem"  @click="getDate('this-week','btn3')" :class="{active: activeBtn === 'btn3'}"><span>This Week</span></div>
    <div class="timeframe_elem disabled" @click="getDate('last-week','btn4')" :class="{active: activeBtn === 'btn4'}"><span>Last Week</span></div>
   <div class="timeframe_elem" @click="getDate('last-month','btn6')" :class="{active: activeBtn === 'btn6'}"><span> Last Month</span></div>
    <div class="timeframe_elem" @click="getDate('custom')"><span>Custom</span></div> -->
  </div>
</template>

<script>
export default {
    name: 'TimeFrame',
    props: {
        active: Boolean,
        isDisable: {
            type: Boolean,
            default: false,
        }
    },
    data() {
        return {
            date: {
                start: '',
                end: '',
            },
            activeBtn: '',

        }
    },
    watch: {
        active() {
            if(!this.active) {
                this.activeBtn = '';
            }
            console.log("activeBtn watch",this.active);
        }
    },
    create() {
        this.activeBtn = this.active;
    },
    methods: {
        getDate(date,active) {
            if(!this.isDisable) {
                console.log("activeBtn");
                this.activeBtn = active;

                this.$emit('activated',true);
                console.log("activeBtn changed",active);

                let today = new Date();
                // console.log(this.formatDate(today))
                switch (date) {
                    case 'today':
                        this.date.start = this.date.end = this.formatDate(today);
                        break;
                    case 'yesterday':
                        this.date.end = this.date.start = this.formatDate(new Date(today.getFullYear(), today.getMonth(), today.getDate() - 1));
                        // this.date.end = this.formatDate(today);
                        console.log(this.date.start,this.date.end )
                        break;
                    case 'this-week':
                        this.date.start = this.formatDate(new Date(today.getFullYear(), today.getMonth(), today.getDate() - 7));
                        this.date.end = this.formatDate(today);
                        console.log(this.date.start,this.date.end)
                        break;
                    case 'last-week':
                        this.user.start = this.user.end = this.formatDate(date);
                        break;
                    case 'this-month':
                        this.date.start = this.formatDate(new Date(today.getFullYear(), today.getMonth(), 1));
                        this.date.end = this.formatDate(today);
                        console.log(this.date.start,this.date.end)
                        break;
                    case 'last-month':
                        this.date.start = this.formatDate(new Date(today.getFullYear(), today.getMonth()-1, 1));
                        this.date.end = this.formatDate(new Date(today.getFullYear(), today.getMonth()-1, new Date(today.getFullYear(),  today.getMonth(), 0).getDate()));
                        console.log(this.date.start,this.date.end)

                        break;
                    case 'custom':
                        console.log(date)
                        break;
                    default:
                        break;
                }
                this.$emit('getDate',this.date);
                return this.date;
            }
        },
        formatDate(date) {
            let d = new Date(date),
                month = '' + (d.getMonth() + 1),
                day = '' + d.getDate(),
                year = d.getFullYear();
            if (month.length < 2) month = '0' + month;
            if (day.length < 2) day = '0' + day;

            return [year, month, day].join('-');
        },
    }
}
</script>

<style>
    .timeframe {
        display: flex;
        max-width: 1170px;
        max-height: 60px;
        box-sizing: border-box;
        margin: 30px auto 30px;
        border-bottom: 1px solid #4C78FA;
        justify-content: space-between;
    }
    .timeframe_elem {
        flex-grow: 1;
        position: relative;
        height: 80px;
        padding: 20px 20px 30px;
        transition: all 0.3s ease;
        min-width: 120px;
        cursor: pointer;
        color: #828282;
    }
    .timeframe_elem span {
        position: absolute;
        left: 50%;
        width: 100%;
        transform: translateX(-50%);
    }
    .timeframe_elem.active {
        transform: scale(1.3);
        color: #3D4BE4;
    }
    .timeframe_elem.active::before {
        color: #3D4BE4;
        background-color: #3D4BE4;

    }
    .timeframe_elem.active::before {
        bottom: 18px; 
    } 
    .timeframe_elem::before {
        content: "";
        position: absolute;
        display: block;
        width: 14px;
        height: 14px;
        border-radius: 50%;
        bottom: 13px;
        left: 50%;
        transform: translate(-50%);
        background-color: #4C78FA;
        transition: all 0.25s ease;
        cursor: pointer;
    }
    .timeframe_elem:hover {
        transform: scale(1.3);
        color: #4C78FA;
    }
    .timeframe_elem:hover::before {
        bottom: 18px;
        left: 50%;
        transform: translate(-50%);
        background-color: #3D4BE4;
    }
        .timeframe_elem.disabled {
        transform: scale(1);
        color: rgb(138, 138, 138);
    }
    .timeframe_elem.disabled:hover {
        transform: scale(1);
        left: none;
        transform: none;
        color: rgb(139, 139, 139);
    }
    .timeframe_elem.disabled::before {
        bottom: 13px;
        background: rgb(139, 139, 139);
    } 
    .timeframe_elem.disabled:hover::before {
        bottom: 13px;
        background: rgb(138, 138, 138);
    }
    @media screen and (max-width: 480px) {
        .timeframe {
            margin: 20px auto;
        }
        .timeframe_elem {
            padding: 20px 5px 20px;
            min-width: 40px;
        }
    }
</style>