<template>
    <div class="admin">
        <div class="admin__name">
            <div class="table__title table__title--name">{{this.geoName}}</div>
            <div class="table__title table__title table__title--center">Leads <span class="table__total">{{this.summDefault}}</span>
            </div>
            <div class="table__title table__title table__title--center">Deposit <span class="table__total">{{this.summDeposit}}</span>
            </div>
        </div>
        <div class="table">
            <div class="table__date">
                <div class="table__date-main">
                    <template v-for="(value, name) in this.geoId.geo" :key="name">
                        <Country :country="value" :countryName="name"/>
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Country  from './Country.vue'
    export default {
        name: "geo",
        components: {
            Country
        },
        props: {
            geoId: Object,
            geoName: String,
        },
        data() {
            return {
                summDefault: 0,
                summDeposit: 0,
            };
        },
        creared() {
            console.log(this.geoId)
            console.log(this.geoName)
        },
        mounted() {
            this.totalData()
            this.$emit("summDefault", this.summDefault);
            this.$emit("summDeposit", this.summDeposit);
        },
        methods: {
            totalData() {
                for(let country in this.geoId.geo) {
                    this.summDeposit += this.geoId.geo[country].Deposit;
                    this.summDefault += this.geoId.geo[country].Default
                }
            },
        }
    }
</script>

