<template>
    <div>
        <TimeFrame 
            :active="activeBtn" 
            :isDisable="isDisable" 
            @getDate="btnDate($event)" 
            @activated="activated($event)"
        />
        <DatePicker v-model="date" :model-config="modelConfig" :max-date="new Date()" is-range>
            <template v-slot="{inputEvents }">
                <div class="datePicker">
                    <input
                        :value="showDate"
                        v-on="inputEvents.start"
                        class="input-date" :class="{'iput-date-invalid': !isValid}"
                        :disabled="isDisable"
                    />
                </div>
            </template>
        </DatePicker>
        <div class="filter-datatype">
            <div @click="switchTypeData('user')" class="btn__dataType btn__dataType--left" :class="{'btn__dataType--active': this.dataType === 'user'}">User</div>
            <div @click="switchTypeData('geo')" class="btn__dataType btn__dataType--right" :class="{'btn__dataType--active': this.dataType === 'geo'}">GEO</div>
            <button class="btn-date" name="button"  @click="sendDate()" :disabled="isDisable">
                <svg width="28" height="28" style="margin: 0 7px" viewBox="0 0 28 28" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M26.863 1.35199C26.8524 1.3007 26.8273 1.25351 26.7908 1.21597C26.7543 1.17843 26.7078 1.15212 26.6568 1.14011C23.2579 0.308836 15.4065 3.27079 11.1482 7.52591C10.3893 8.27859 9.69731 9.09586 9.08004 9.96848C7.7675 9.84973 6.45497 9.94973 5.33744 10.4372C2.18172 11.8248 1.26357 15.4486 1.00732 17.0043C0.993432 17.0895 0.999363 17.1768 1.02465 17.2594C1.04993 17.342 1.09389 17.4177 1.15312 17.4805C1.21235 17.5434 1.28526 17.5917 1.36619 17.6219C1.44713 17.652 1.53392 17.6631 1.61983 17.6543L6.68685 17.0981C6.69036 17.4802 6.7133 17.862 6.7556 18.2418C6.78205 18.5044 6.89919 18.7495 7.08686 18.935L9.04879 20.8969C9.23444 21.0847 9.4798 21.2019 9.74256 21.2282C10.1201 21.2703 10.4996 21.2932 10.8795 21.2969L10.3238 26.3596C10.3152 26.4454 10.3263 26.532 10.3565 26.6128C10.3866 26.6936 10.435 26.7664 10.4977 26.8255C10.5605 26.8847 10.636 26.9286 10.7185 26.9539C10.8009 26.9792 10.8881 26.9852 10.9732 26.9715C12.5295 26.7215 16.1609 25.8033 17.5403 22.6482C18.0278 21.5294 18.1278 20.2238 18.0153 18.9175C18.8905 18.3008 19.7101 17.6088 20.4647 16.8493C24.7386 12.5992 27.6818 4.92022 26.863 1.35199ZM16.6659 11.3435C16.2926 10.9703 16.0383 10.4948 15.9353 9.97705C15.8323 9.45933 15.8851 8.92269 16.0871 8.43499C16.2891 7.94729 16.6311 7.53044 17.07 7.23716C17.5089 6.94387 18.0249 6.78734 18.5528 6.78734C19.0807 6.78734 19.5967 6.94387 20.0356 7.23716C20.4745 7.53044 20.8166 7.94729 21.0185 8.43499C21.2205 8.92269 21.2733 9.45933 21.1703 9.97705C21.0673 10.4948 20.813 10.9703 20.4397 11.3435C20.1921 11.5917 19.898 11.7885 19.5742 11.9228C19.2504 12.0571 18.9033 12.1263 18.5528 12.1263C18.2023 12.1263 17.8552 12.0571 17.5314 11.9228C17.2076 11.7885 16.9135 11.5917 16.6659 11.3435V11.3435Z" stroke="white" stroke-linecap="round" stroke-linejoin="round"/>
                    <path d="M4.85179 19.9894C4.22872 20.0659 3.64906 20.3483 3.20487 20.7919C2.10358 21.8969 1.99921 25.9896 1.99921 25.9896C1.99921 25.9896 6.09432 25.8852 7.19623 24.7826C7.6416 24.3392 7.92454 23.7585 7.99937 23.1345" stroke="white" stroke-linecap="round" stroke-linejoin="round"/>
                </svg>
            Run</button>
        </div>
    </div>
</template>

<script>
import TimeFrame from "./TimeFrame.vue"
import { DatePicker } from 'v-calendar';
export default {
    name: 'PanelControl',
    props: {
        isValid: {
            type: Boolean,
            default: true,
        },
        isDisable: {
            type: Boolean,
            default: false,
        }
    },
    components: {
        TimeFrame,
        DatePicker,
    },
    data(){
        return {
            date: {
                start: '',
                end: ''
            },
            modelConfig: {
                type: 'string',
                mask: 'YYYY-MM-DD', // Uses 'iso' if missing
            },
            validationfield: false,
            activeBtn: false,
            disableBtn: false,
            dataType: 'user',
        }
    },
    watch: {
        dataType() {
            if(this.date.start && this.date.end) {
                this.sendDate();
            }
        }
    },
    computed: {
        showDate() {
            if(this.date.start && this.date.end) {
                return `${this.date.start}  -  ${this.date.end}`;
            }
            else {
                return `From  -  To`;
            }
        }
    },
    methods: {
        btnDate(event) {
            this.date.start = event.start;
            this.date.end = event.end;

            this.$emit('dataType',this.dataType)
            this.$emit('date',event);
            this.$emit('clicked');
        },
        switchTypeData(type) {
            if(!this.isDisable) {
                this.dataType = type;
            }
        },
        sendDate() {
            this.activeBtn = false
            this.$emit('dataType',this.dataType)
            this.$emit('date', this.date);
            this.disableBtn = true;
            this.$emit('clicked');
            console.log("sendDate");
        },
        activated() {
            this.activeBtn = true;
            console.log("activeted");
        },
    }
}
</script>
<style>
.input-date {
    padding: 10px;
    border: 2px solid #0076e9;
    border-radius: 5px;
    margin: 10px;
    cursor: pointer;
    text-align: center;
    font-size: 20px;
    transition: border 0.3s ease;
}
.iput-date-invalid {
    border: 2px solid #e9001f;

}
.btn-date {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 20px;
    background: #4C78FA;
    max-width: 170px;
    width: 100%;
    border: none;
    margin: 0 20px;
    border-radius: 10px;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    transition: all 0.3s ease;
}
.btn-date:hover {
    background:#0076e9;

}
.btn-date:disabled {
    background: #a9a9a9;
    cursor: auto;
}
.filter-datatype {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    margin: 20px 0;
    width: 100%;

}
.btn__dataType {
    max-width: 100px;
    width: 100%;
    text-decoration: none;
    padding: 10px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    /*transition: all .3s ease;*/
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    border: 1px solid #4C78FA;
    color:#4C78FA;
    line-height: 27px;
    cursor: pointer;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);

}
.btn__dataType--left {
    border-radius: 10px 0 0 10px;
}
.btn__dataType--right {
    border-radius: 0 10px 10px 0;
}
.btn__dataType--active {
    background: #4C78FA;
    color: #fff;
}
.datePicker {
    display: flex;
    justify-content: center;
    align-items: center;
}
.datePicker__svg {
    width: 25px;
}
</style>