<template>
    <div class="admin">
        <div class="admin__name">
            <div class="table__title table__title--name">{{this.affiliateName}}</div>
            <div class="table__title table__title table__title--center">Leads <span class="table__total">{{this.summDefault}}</span>
            </div>
            <div class="table__title table__title table__title--center">Deposit <span class="table__total">{{this.summDeposit}}</span>
            </div>
        </div>
        <div class="table">
            <div class="table__date">
                <div class="table__date-main">
                    <template v-for="(value, name) in this.affiliateId.sourceid" :key="name">
                        <Source :sourceId="value" :SourceIdName="name" @summDefault="getSummDefault($event)"
                            @summDeposit="getSummDeposit($event)" />
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Source  from './Source.vue'
    export default {
        name: "Affiliate",
        components: {
            Source
        },
        props: {
            affiliateId: Object,
            affiliateName: String,
        },
        data() {
            return {
                summDefault: 0,
                summDeposit: 0,
            };
        },
        mounted() {
            this.$emit("summDefault", this.summDefault);
            this.$emit("summDeposit", this.summDeposit);
        },
        methods: {
            getSummDefault(event) {
                this.summDefault += event
            },
            getSummDeposit(event) {
                this.summDeposit += event
            }
        }
    }
</script>

