<template>
    <table class="table">
        <template v-for="(value, name) in this.leads.name" :key="name">
            <Affiliate v-if="this.typeData === 'user'" :affiliateId="value" :affiliateName="name" @summDefault="getSummDefault($event)" @summDeposit="getSummDeposit($event)" />
            <Geo v-if="this.typeData === 'geo'" :geoId="value" :geoName="name" @summDefault="getSummDefault($event)" @summDeposit="getSummDeposit($event)" />
        </template>
        <Total :total="this.total" :detailsData="this.detailsData"/>
    </table>
</template>

<script>
// import * as Vue from 'vue'
import Affiliate  from './Affiliate.vue'
import Total from './Total.vue'
import Geo from './Geo.vue'


export default {
    name: 'TableInfo',
    props: {
        leads: Object,
        typeData: String,
    },
    components: {
        Affiliate,
        Total,
        Geo,
    },
    data() {
        return {
            total: {
                summDefault: 0,
                summDeposit: 0,
            },
            detailsData: {}
        };
    },
    created() {
        this.getTotalData(this.typeData);
    },
    methods: {
        getSummDefault(event) {
            this.total.summDefault += event;
            console.log("getSummDefault");
        },
        getSummDeposit(event) {
            this.total.summDeposit += event;
            console.log("getSummDeposit");
        },
        getTotalData(select) {
            console.log("getTotalData");
            this.detailsData = {};
            switch (select) {
                case 'geo':
                    for(let value in this.leads.name) {
                        let sources = this.leads.name[value];
                        for(let elem in sources.geo) {
                            if(!this.detailsData[elem]) this.detailsData[elem] = {};
                            let country = sources.geo[elem];
                            if (Object.prototype.hasOwnProperty.call(this.detailsData,elem)){
                                this.detailsData[elem].deposit = this.detailsData[elem].deposit ? this.detailsData[elem].deposit : 0;
                                this.detailsData[elem].default = this.detailsData[elem].default ? this.detailsData[elem].default : 0;
                            }
                            this.detailsData[elem].deposit += country.Deposit
                            this.detailsData[elem].default += country.Default;
                        }
                    }
                    break;
                case 'user':
                    for(let value in this.leads.name) {
                        let sources = this.leads.name[value];
                        for(let source in sources.sourceid) {
                            if(!this.detailsData[source]) this.detailsData[source] = {}
                            let affSubs =  sources.sourceid[source];
                            if (Object.prototype.hasOwnProperty.call(this.detailsData,source)){
                                this.detailsData[source].deposit = this.detailsData[source].deposit ? this.detailsData[source].deposit : 0;
                                this.detailsData[source].default = this.detailsData[source].default ? this.detailsData[source].default : 0;
                            }
                            for(let elem in affSubs.affsub) {
                                this.detailsData[source].deposit += affSubs.affsub[elem].Deposit
                                this.detailsData[source].default += affSubs.affsub[elem].Default;
                            }
                        }
                    }
                    console.log(this.detailsData)
                    break;
                default:
                    break;
            }
        }
    }

}
</script>

<style>

.admin {
    background: rgba(255,255,255,0.6);
    backdrop-filter: blur(3px);
    display: flex;
    flex-flow: column nowrap;
    width: 100%;
    max-width: 1170px;
    padding: 70px;
    border: 1px solid #3D4BE4;
    border-radius: 30px;
    margin: 0 auto 70px;
}

.admin__name {
    display: flex;
    text-align: center;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size: 51px;
    line-height: 62px;
    color: #3E3E3E;
}
.table {
    margin: 10px;
    width: 100%;
    margin: 0 auto;
}
.table__title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.table__date-header {
    background: rgb(230, 241, 253);
    padding: 10px;
}
.table__source {
    padding: 10px 0;
}
.table__source-header {
    padding: 10px 25px;
    margin: 0;
    display: flex;
    text-align: left;
    cursor: pointer;
    background-color: #F6F6F6;
    border-radius: 14px;
    font-style: normal;
    font-weight: normal;
    font-size: 33px;
    line-height: 40px;
}
.table__source-title {
    width: 100%;
}
.table__title--name {
    text-transform: capitalize;
    justify-content: flex-start;
}
.table__source-title--name,.table__title--name,.table__affsub-value--name {
    max-width: 320px;
    width: 100%;
}
.table__affsub-value,.table__title--name {
    text-align: left;
}
.table__source-title--center, .table__title--center, .table__affsub-value--center {
    flex-grow: 1;
    text-align: center;
}
.active-table,
.table__source-titlen:hover {
    background-color: #F6F6F6;
    color: #424242;
}
.table__affsub-container {
    width: 100%;
    display: flex;
    flex-flow: column nowrap;
    transition: all 0.2s ease-out;
}
.table__affsub {
    box-sizing: border-box;
    background: transparent;
    display: flex;
    width: 100%;
    color: #616161;
    font-size: 23px;
    line-height: 28px;
    border-bottom: 1px solid #EFEFEF;
    padding: 10px 25px;

}
.table__affsub-header,
.table__affsub-main {
    display: flex;   
}
.table__affsub-header {
    font-weight: bold;
    background: rgb(245, 207, 207);
}
.table__affsub-main {
    flex-flow: column nowrap;
}
.table__affsub-title,
.table__affsub-value {
    width: 100%;
    min-width: 100px;
}
.table__total {
    background: #4C78FA;
    border-radius: 12px;
    max-height: 70px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 10px;
    color: #fff;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}
    @media screen and (max-width: 768px) {
        .admin {
            padding: 25px;
            overflow-x: auto;
        }
        .admin__name{
            font-size: 25px;
            line-height: 1;
        }
        .table__source-header {
            padding: 10px;
        }
        .table__source-header {
            font-size: 22px;
        }
        .total {
            padding: 10px 25px;
        }
        .total__item {
            font-size: 22px;
        }
        .total-details {
            font-size: 22px;
            padding: 10px 25px;
        }
    }
</style>