<template>
<div class="v-spinner" v-show="loading">
    <div class="v-pulse v-pulse1" v-bind:style="[spinnerStyle,spinnerDelay1]">
    </div><div class="v-pulse v-pulse2" v-bind:style="[spinnerStyle,spinnerDelay2]">
    </div><div class="v-pulse v-pulse3" v-bind:style="[spinnerStyle,spinnerDelay3]">
    </div>
</div>
</template>

<script>
export default {
    name: 'PulseLoader',

    props: {
        loading: {
        type: Boolean,
        default: true
        },
        color: { 
        type: String,
        default: '#0076e9'
        },
        size: {
        type: String,
        default: '25px'
        },
        margin: {
        type: String,
        default: '10px'
        },
        radius: {
        type: String,
        default: '100%'
        }
    },
    data () {
        return {
            spinnerStyle: {
                backgroundColor: this.color,
                width: this.size,
                height: this.size,
                margin: this.margin,
                borderRadius: this.radius,
                display: 'inline-block',
                animationName: 'v-pulseStretchDelay',
                animationDuration: '1s',
                animationIterationCount: 'infinite',
                animationTimingFunction: 'cubic-bezier(.2,.68,.18,1.1)',
                animationFillMode: 'both'
            },
            spinnerDelay1: {
                animationDelay: '0.12s'
            },
            spinnerDelay2: {
                animationDelay: '0.24s'
            },
            spinnerDelay3: {
                animationDelay: '0.36s'
            }
        }
    }
}
</script>

<style>
.v-spinner
{
    margin: 100px auto;
    text-align: center;
}


@-webkit-keyframes v-pulseStretchDelay
{
    0%,
    80%
    {
        -webkit-transform: scale(1);
                transform: scale(1);
        -webkit-opacity: 1;             
                opacity: 1;
    }
    45%
    {
        -webkit-transform: scale(0.01);
                transform: scale(0.01);
        -webkit-opacity: 0.7;             
                opacity: 0.7;
    }
}

@keyframes v-pulseStretchDelay
{
    0%,
    80%
    {
        -webkit-transform: scale(1);
                transform: scale(1);
        -webkit-opacity: 1;             
                opacity: 1;
    }
    45%
    {
        -webkit-transform: scale(0.01);
                transform: scale(0.01);
        -webkit-opacity: 0.7;             
                opacity: 0.7;
    }
}
</style>