<template>
  <transition name="fade">
    <router-view />
  </transition>
</template>

<script>
export default {
  name: 'App',

  data() {
    return {
      authenticated: false,
      token: '',
    }
  },
  mounted() {
    if(this.authenticated) {
      this.$emit("authenticated", true);
      this.$router.replace({name: "Login"});
    }
  },
  methods: {
      setAuthenticated(status) {
        this.authenticated = status;
      },
      logout() {
        this.authenticated = false;
      },
      getToken(token) {
        this.token = token
      }
  }
}
</script>

