<template>
  <div class="app__container">
      <div class="login">
        <h1 class="login__title">{{title}}</h1>
        <form class="login__form">
          <input class="login__field login__field--name" v-model="input.username" type="text" name="username" placeholder="Login" required>
          <input class="login__field login__field--pass" v-model="input.password" type="password" neme="password" placeholder="Password" required>
          <button class="login__buttn login__buttn--fill" type="button" @click="login">Login</button>
          <button class="login__buttn" type="button" @click="register">Register</button>
        </form>
      </div>
  </div>

</template>

<script>
export default {
  name: 'Login',
  props: {
  },
  data() {
    return {
      input: {
        username: '',
        password: ''
      },
      invite: '',
      stateLogin: true,
      title: "Sign In"
    }
  },
  watch: {
    stateLogin: function(){
      this.title = this.stateLogin ? "Sign In" : "Sign Up";
    }
  },
  created() {
    const queryString = window.location.search;
    const urlParams = new URLSearchParams(queryString);
    const product = urlParams.get('invite');
    console.log(queryString,"+",product);
    this.invite = product;
  },
  methods: {
    async login() {
      if(this.stateLogin) {
        const baseURI = 'https://api.notasscum.com/login';
        if(this.input.username != '' && this.input.password != '') {
          await this.axios.post(baseURI, {
            username: this.input.username,
            password: this.input.password
          }).then(res => {
              return res.data;
          }).then(data => {
            if(data.data.token) {
            console.log(data);
              this.token = data.data.token;
              this.$emit('authenticated', true);
              this.$emit("getToken", data.data.token);
              sessionStorage.setItem('token',data.data.token);
              sessionStorage.setItem('username',this.input.username);
              this.$router.replace({name: "Profile"});
            }
            else {
              this.$emit('authenticated', false);
              this.$router.replace({name: "Login"});
              console.log("The username and / or password is incorrect");
            }
          }).catch(err => {
            console.log(err)
            this.$router.replace({name: "Login"});
          });
        }
        else {
          alert("field is empty")
        }        
      }
      else {
        console.log("go to login");
        this.stateLogin = true;
        this.input.username = '';
        this.input.password = '';

      }
    },
    async register() {
      if(!this.stateLogin) {
        const baseURI = 'https://api.notasscum.com/register'
        if(this.input.username != '' && this.input.password != '') {
          await this.axios.post(baseURI, {
            username: this.input.username,
            password: this.input.password,
            invite: this.invite,
          }).then(res => {
              console.log(res);
              return res.data;
          }).then(data => {
            if(data) {
            console.log(data);
              // this.token = data.data.token;
              // this.$emit('authenticated', true);
              // this.$emit("getToken", data.data.token);
              // sessionStorage.setItem('token',data.data.token);
              // sessionStorage.setItem('username',this.input.username);
              // this.$router.replace({name: "Profile"});
            }
            else {
              // this.$emit('authenticated', false);
              // this.$router.replace({name: "Login"});
              // console.log("The username and / or password is incorrect");
            }
          }).catch(err => {
            console.log(err)
            this.$router.replace({name: "Login"});
          });
        }
        else {
          alert("field is empty")
        }
      }
      else {
        this.stateLogin = false
        console.log("go to register");
        this.input.username = '';
        this.input.password = '';
      }
    }   
  }
}
</script>

