<template>
    <div class="table__source">
        <div class="table__source-header" @click="toggleSection()" >
            <div class="table__source-title table__source-title--name">{{this.SourceIdName}}</div>
            <div class="table__source-title table__source-title--center">{{this.summDefault}}</div>
            <div class="table__source-title table__source-title--center">{{this.summDeposit}}</div>
        </div>
            <div v-show="isOpen" class="table__affsub-container" >
                <div class="table__affsub" v-for="(value, name) in this.sourceId.affsub" :key="name">
                    <div class="table__affsub-value table__affsub-value--name">{{name}}</div>
                    <div class="table__affsub-value table__affsub-value--center">{{value.Default}}</div>
                    <div class="table__affsub-value table__affsub-value--center">{{value.Deposit}}</div>
                </div>
            </div>
    </div>
</template>

<script>
export default {
    name: 'Source',
    props: {
        sourceId: Object,
        SourceIdName: String
    },
    data() {
        return {
            summDeposit: 0,
            summDefault: 0,
            isOpen: false,
        }
    },
    mounted() {
        this.totalData()
    },
    methods: {
        totalData() {
            for(let affsub in this.sourceId.affsub) {
                this.summDeposit += this.sourceId.affsub[affsub].Deposit;
                this.summDefault += this.sourceId.affsub[affsub].Default
            }
            this.$emit("summDefault", this.summDefault);
            this.$emit("summDeposit", this.summDeposit);

        },
        toggleSection() {
            this.isOpen = !this.isOpen;
        }
    },

}
</script>

