<template>
  <div >
    <Header/>
    <PanelControl :isValid="isValid" 
                  :isDisable="isDisable" 
                  @date="getCustomDate($event)" 
                  @clicked="getReport()" 
                  @dataType="this.key = $event"/>
    <preloader 
        :loading="showPreloader" />
    <TableInfo v-if="response" 
              :leads="this.leads" 
              :typeData="this.key"/>
  </div>
</template>

<script>
import Header from '@/components/Header.vue'
import PanelControl from '../components/PanelControl.vue'
import TableInfo from '../components/TableInfo.vue'
import Preloader from '../components/Preloader.vue'
export default {
    name: 'Profile',
    components: {
      Header,
      TableInfo,
      PanelControl,
      Preloader
    },
    data() {
        return {
          user: {
            name: '',
            dateFrom: '',
            dateTo: '',
            token: '',
            modelValue: ''
          },
          key: '',
          isValid: true,
          isDisable: false,
          showPreloader: false,
          response: false,
          leads: Object
        };
    },
    computed: {
      dateFrom: function(){
        // console.log("dateFrom changed");
        return this.dateFrom;
      },
      dateTo: function() {
        // console.log("dateTo changed");
        return this.dateTo;
      }
    },
    created() {
      this.user.name = sessionStorage.getItem('username');
      this.user.token = sessionStorage.getItem('token');
    },
    methods: {
      getCustomDate(event) {
        // console.log(event.fromDate);
        // console.dir(event.toDate);
        this.user.dateFrom = event.start;
        this.user.dateTo = event.end;
      },
      getReport() {
        this.response = false;
        // console.log("getReport")
        if(this.user.dateFrom && this.user.dateTo) {
          this.showPreloader = true;
          this.isDisable = true;
          this.isValid = true;
          switch (this.key) {
            case "user":
              this.axios.post('https://api.notasscum.com/sub_report', {
                username: this.user.name,
                token:  this.user.token,
                dateFrom: this.user.dateFrom,
                dateTo: this.user.dateTo
              }).then(res => {
                let info = res.data;
                return info;
              }).then(info => {
                if(info.code === 1) {
                  this.response = true;
                  this.leads = info.data;
                  this.showPreloader = false;
                }
                else {
                  console.log(info);
                  this.showPreloader = false;
                  alert(info.data)
                }
                this.isDisable = false;
              })
              break;
            case "geo":
              this.axios.post('https://api.notasscum.com/geo_report', {
                username: this.user.name,
                token:  this.user.token,
                dateFrom: this.user.dateFrom,
                dateTo: this.user.dateTo
              }).then(res => {
                return res.data
              }).then(data => {
                this.showPreloader = false;
                if(data.data) {
                  // console.log(data);
                  this.response = true;
                  this.leads = data.data;
                }
                else {
                  // console.log(data);
                  alert(data.data)
                }
                this.isDisable = false;
              })
              break;
            default:
              console.log("error")
              break;
          }
        }
        else {
          this.isValid = false;
          console.log("out of data");
        }
      },
    }
}
</script>

