import {
    createWebHistory,
    createRouter
} from "vue-router";
import Login from "@/pages/Login.vue";
import Profile from "@/pages/Profile.vue";
import Setting from "@/pages/Setting.vue";


const routes = [
    {
        path: '/',
        name: "Home",
        redirect: {
            name: "Login"
        },
    },
    {
        path: "/login",
        name: "Login",
        component: Login,
        beforeEnter: (to, from, next) => {
            if (!sessionStorage.getItem('token')) {
                next();
            } else {
                next({
                    name: 'Profile'
                })
            }
        }
    },
    {
        path: "/profile",
        name: "Profile",
        component: Profile,
    },
        {
        path: "/setting",
        name: "Setting",
        component: Setting,
    },
    {
        path: "/:pathMatch(.*)*",
        name: "NotFound",
        redirect: 'Login',
    }
];

const router = createRouter({
    routes,
    history: createWebHistory()
});
router.beforeEach((to, from, next) => {
    if (to.name !== "Login") {
        if (sessionStorage.getItem('token')) {
            next();
        } else {
            next({
                name: 'Login'
            })
        }
    }
    next();
})
export default router;